import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: () => import("../components/main"),
    redirect: "/pv",
    children: [
      // {
      //   path: "/bigscreen2",
      //   name: "bigscreen2",
      //   component: () => import("../components/bigscreen2/index"),
      //   meta: { title: "主屏" },
      // },
      {
        path: "pv",
        name: "pv",
        component: () => import("../components/datav/index"),
        meta: { title: "光伏管理系统" },
      },
      {
        path: "energyStorage",
        name: "energyStorage",
        component: () => import("../components/energyStorage/index"),
        meta: { title: "储能管理系统" },
      },
      {
        path: "charge",
        name: "charge",
        component: () => import("../components/charge/index"),
        meta: { title: "充电管理系统" },
      },
      {
        path: "distribution",
        name: "distribution",
        component: () => import("../components/distribution/index"),
        meta: { title: "配电管理系统" },
      },
      {
        path: "equipment",
        name: "equipment",
        component: () => import("../components/equipment/index"),
        meta: { title: "重点用能设备" },
      },
      {
        path: "kPI",
        name: "kPI",
        component: () => import("../components/KPI/index"),
        meta: { title: "能源绩效考核" },
      },
    ],
  },
];
const router = new VueRouter({
  mode: "history",
  routes,
});
export default router;
